<template>
  <div class="c-create-inventory">
    <form class="form-wrap" onsubmit="return false;" @submit="onSave">
      <label class="form-item">
        <span class="label"></span>
        <el-cascader
          v-model="inventory.category"
          :options="categories"
          filterable
          change-on-select
          placeholder=""
        />
        <el-button tabindex="-1" class="more" type="text"></el-button>
      </label>
      <label class="form-item">
        <span class="label"></span>
        <el-input
          ref="name"
          v-model="inventory.name"
          autofocus
          clearable
        />
      </label>
      <label class="form-item">
        <span class="label"></span>
        <el-input-number
          v-model="inventory.count"
          :min="0"
          size="small"
        />
        <el-radio-group v-model="inventory.unit" size="small">
          <el-radio-button label="" />
          <el-radio-button label="" />
          <el-radio-button label="" />
        </el-radio-group>
      </label>
      <label class="form-item">
        <span class="label"></span>
        <el-date-picker
          v-model="inventory.expired"
          align="right"
          :picker-options="expiredOptions"
        />
      </label>
      <label class="form-item">
        <span class="label"></span>
        <el-input
          v-model="inventory.remarks"
          :autosize="{minRows: 3, maxRows: 10}"
          type="textarea"
          placeholder="..."
        />
      </label>
      <button class="hidden" type="submit" />
    </form>

    <button
      class="help"
      type="button"
      tabindex="-1"
      @click="showHelp = !showHelp"
    >
       <i class="el-icon-question" />
    </button>
    <el-collapse-transition>
      <el-card v-if="showHelp" class="description">
        <h2></h2>
        <p>
          
          <br>
          
        </p>
        <hr>
        <p>
          <strong></strong>
          
          <br>
          <small class="tips">tips: </small>
        </p>
        <p>
          <strong></strong>
          
        </p>
        <p>
          <strong>&</strong>
          
        </p>
        <p>
          <strong></strong>
          
          <br>
          <small class="tips">tips:  1 1 </small>
        </p>
        <p>
          <strong></strong>
          
        </p>
      </el-card>
    </el-collapse-transition>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
import { types } from '@/stores/inventory'
import { onFuture } from '@/utils/datetimePicker'

export default {
  name: 'CreateInventory',
  data () {
    return {
      inventory: {
        category: [],
        name: '',
        count: 1,
        unit: '',
        expired: new Date(),
        remarks: '',
      },
      showHelp: false,
      expiredOptions: {
        shortcuts: [
          { text: '', onClick: onFuture(1) },
          { text: '3', onClick: onFuture(3) },
          { text: '1', onClick: onFuture(7) },
          { text: '2', onClick: onFuture(14) },
          { text: '1', onClick: onFuture(30) },
        ],
      },
    }
  },
  computed: {
    ...mapState('inventory', { originCategories: 'categories' }),
    categories () {
      const origin = this.$store.state.inventory.categories
      const categories = origin.map(item => ({
        parent: item.parent,
        label: item.label,
        value: item.label,
      }))
      return this.convertCategories(categories, '')
    },
  },
  created () {
    this.$parent.$on('save', this.onSave)
  },
  methods: {
    ...mapMutations('inventory', {
      'updateList': types.UPDATE_LIST,
    }),
    onSave () {
      this.updateList({
        ...this.inventory,
        updateAt: +new Date(),
      })
      this.clear()
      this.$refs.name.focus()
    },
    clear () {
      this.inventory.name = ''
      this.inventory.count = 1
      this.inventory.remarks = ''
    },
    /**
     * 
     */
    convertCategories (array, parent) {
      const result = []
      for (const category of array) {
        if (category.parent === parent) {
          const children = this.convertCategories(array, category.label)
          if (children.length) category.children = children
          result.push(category)
        }
      }
      return result
    },
  },
}
</script>

