<template>
  <el-collapse-item class="setting-item" name="menstrual">
    <span slot="title" :class="{ disabled: !enable }" class="item-title"></span>
    <el-form class="item-content" label-width="6em" label-position="left">
      <el-form-item label="">
        <el-switch :value="enable" @change="toggleEnable" />
      </el-form-item>
      <el-collapse-transition>
        <div v-show="enable">
          <el-form-item label="">
            <el-date-picker
              v-model="date"
              type="date"
              placeholder=""
              size="small"
            />
          </el-form-item>
          <el-form-item label="">
            <el-input-number
              :value="cycle"
              :min="1"
              size="small"
              @change="updateCycle"
            /> 
          </el-form-item>
          <el-form-item label="">
            <el-input-number
              :value="during"
              :min="1"
              size="small"
              @change="updateDuring"
            /> 
          </el-form-item>
        </div>
      </el-collapse-transition>
      <el-card>
        <p>
          
          <br>
          
          
        </p>
        <p>  </p>
      </el-card>
    </el-form>
  </el-collapse-item>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('menstrual')

export default {
  name: 'SettingMenstrual',
  computed: {
    ...mapState(['enable', 'lastDate', 'cycle', 'during']),
    date: {
      get () {
        return this.lastDate
      },
      set (val) {
        this.updateDate(val)
      },
    },
  },
  methods: {
    ...mapActions([
      'toggleEnable',
      'updateDate',
      'updateCycle',
      'updateDuring',
    ]),
  },
}
</script>
